<template>
  <div
    class="bookmark"
    @click.stop
    @tap.stop="onBookmarkTapped"
    @press.stop="onBookmarkPressed"
  >
    <slot name="icon">
      <VanImage
        round
        width="48px"
        height="48px"
        fit="cover"
        :src="iconSrc"
      />
    </slot>
    <transition
      enter-active-class="animate__animated animate__faster animate__bounceIn"
      leave-active-class="animate__animated animate__faster animate__bounceOut"
    >
      <Icon
        v-show="removable"
        class="bookmark-close"
        name="cross"
        color="white"
        @click.stop
        @tap.stop="onButtonRemoveTapped"
      />
    </transition>
    <div
      class="bookmark-title"
      :style="titleStyle"
    >
      {{title}}
    </div>
  </div>
</template>

<style scoped>
.bookmark {
  display: inline-block;
  vertical-align: top;
  padding: 8px 0;
  width: 48px;
  font-size: 0;
  text-align: center;
}

.bookmark-close {
  display: block;
  position: absolute;
  top: 4px;
  right: -4px;
  width: 20px;
  font-size: 12px;
  font-weight: bold;
  line-height: 20px;
  background-color: rgb(0, 0, 0, .8);
  border-radius: 50%;
}

.bookmark-title {
  margin-top: 8px;
  font-size: 12px;
  line-height: 1;
}
</style>

<script>
import AnyTouch from 'any-touch'
import { Icon, Image as VanImage } from 'vant'

export default {
  name: 'BaseBookmark',
  components: {
    Icon,
    VanImage
  },
  props: {
    url: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    titleColor: {
      type: String,
      default: 'black'
    },
    iconSrc: {
      type: String,
      default: ''
    },
    removable: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    titleStyle () {
      return {
        color: this.titleColor
      }
    }
  },
  mounted () {
    // 初始化手势插件
    this.initAnyTouch()
  },
  methods: {
    // 初始化手势插件
    initAnyTouch () {
      const at = new AnyTouch(this.$el)
      this.$on('hook:destroyed', () => {
        at.destroy()
      })
    },
    // 书签点击事件
    onBookmarkTapped () {
      this.$emit('tap')
    },
    // 书签长按事件
    onBookmarkPressed () {
      this.$emit('press')
    },
    // 移除按钮点击事件
    onButtonRemoveTapped () {
      this.$emit('remove')
    }
  }
}
</script>
