<template>
  <div class="text2icon-container">
    <VanImage
      class="text2icon-image"
      round
      width="80"
      height="80"
      fit="cover"
      :src="iconData.iconDataURL"
    />
    <canvas
      ref="text2iconCanvas"
      class="text2icon-canvas"
      width="256"
      height="256"
    ></canvas>
    <div class="text2icon-color-picker-wrapper">
      <ColorPicker
        class="text2icon-color-picker"
        :value="iconColor"
        :palette="palette"
        @input="onColorsChanged"
      />
    </div>
  </div>
</template>

<style scoped>
.text2icon-container {
  display: flex;
  align-items: center;
}

.text2icon-container > [class^="text2icon-"] {
  margin: 0 8px 8px 0;
}

.text2icon-canvas {
  display: none;
}

.text2icon-color-picker-wrapper {
  display: flex;
  align-items: center;
  width: 80px;
  height: 80px;
  background-color: #f7f8fa;
  border-radius: 8px;
}

.text2icon-color-picker {
  margin: auto;
  width: 65px;
  vertical-align: middle;
  background-color: unset;
  box-shadow: unset;
}
</style>

<script>
import { Image as VanImage } from 'vant'
import { Compact as ColorPicker } from 'vue-color'

export default {
  name: 'BaseText2Icon',
  components: {
    VanImage,
    ColorPicker
  },
  data () {
    return {
      iconData: {}
    }
  },
  props: {
    iconText: String,
    iconColor: {
      type: String,
      default: '#f44336'
    },
    palette: {
      type: Array,
      default () {
        return ['#f44336', '#e91e63', '#9c27b0', '#673ab7', '#3f51b5', '#2196f3', '#03a9f4', '#00bcd4', '#009688']
      }
    }
  },
  watch: {
    // 监听输入颜色改变
    iconColor (value) {
      this.drawTextIcon(this.iconText, this.iconColor).then(this.updateIconData)
    },
    // 监听输入文字改变
    iconText (value) {
      this.drawTextIcon(this.iconText, this.iconColor).then(this.updateIconData)
    }
  },
  mounted () {
    this.drawTextIcon(this.iconText, this.iconColor).then(this.updateIconData)
  },
  methods: {
    // 暴露图标文件信息
    updateIconData (iconData) {
      // 转换blob为file
      this.iconData = iconData
      // 触发图片文件更新事件
      this.$emit('icon-data-updated', iconData)
    },
    // 改变canvas状态
    drawTextIcon (text, color) {
      // 相关常量与变量
      const canvasEle = this.$refs.text2iconCanvas
      const context = canvasEle.getContext('2d')
      // 填充图片背景
      context.fillStyle = color
      context.arc(128, 128, 128, 0, Math.PI * 2, false)
      context.fill()
      // 填写文字
      const textFirst = text.length > 0 ? text.substr(0, 1) : '简'
      context.fillStyle = '#eeeeee'
      context.font = '128px Arial'
      context.textAlign = 'center'
      context.textBaseline = 'middle'
      context.fillText(textFirst, 128, 128)
      // 暴露iconData
      return new Promise(resolve => {
        const iconType = 'image/png'
        canvasEle.toBlob(blob => {
          const iconData = {
            iconFile: new File([blob], 'text-icon.png', {
              type: iconType
            }),
            iconDataURL: canvasEle.toDataURL(iconType, 1)
          }
          return resolve(iconData)
        }, iconType, 1)
      })
    },
    // 响应改变颜色事件
    onColorsChanged (colors) {
      const hexColor = colors.hex
      this.$emit('icon-color-changed', hexColor)
    }
  }
}
</script>
