<template>
  <transition-group
    name="search-suggestion"
    tag="ul"
  >
    <li
      v-for="suggestion of searchSuggestion"
      :key="suggestion"
      class="search-suggestion-item"
      @click.stop="onSuggestionItemClicked(suggestion)"
    >
      <Icon
        name="search"
        class="search-suggestion-item-left-icon"
      />
      <div class="search-suggestion-item-content van-ellipsis">
        {{suggestion}}
      </div>
      <Icon
        name="down"
        class="search-suggestion-item-right-icon"
      />
    </li>
  </transition-group>
</template>

<style scoped>
.search-suggestion-enter-active {
  animation: fadeInUp 0.5s both;
}

.search-suggestion-leave-active {
  position: absolute !important;
  animation: fadeOutDown 0.5s both;
}

.search-suggestion-move {
  transition: transform 0.5s;
}

.search-suggestion-item {
  display: flex;
  align-items: center;
  padding: 0 8px;
  border-top: 1px solid var(--border-color-darker);
}

.search-suggestion-item-content {
  flex: 1;
  margin: 0 8px;
  font-size: 14px;
  line-height: 40px;
}

.search-suggestion-item-left-icon {
  font-size: 20px;
  color: var(--icon-color);
}

.search-suggestion-item-right-icon {
  font-size: 16px;
  font-weight: 100;
  color: var(--icon-color);
  transform: rotate(-135deg);
}
</style>

<script>
import { Icon } from 'vant'

export default {
  name: 'PageHomeSearchSuggestion',
  components: {
    Icon
  },
  props: {
    searchSuggestion: {
      type: Array,
      required: true
    }
  },
  methods: {
    onSuggestionItemClicked (suggestion) {
      this.$emit('suggestion-item-clicked', suggestion)
    }
  }
}
</script>
