<template>
  <transition-group
    name="search-history"
    tag="div"
  >
    <span
      v-for="(history, index) of searchHistory"
      :key="history"
      class="van-ellipsis"
      @tap.stop="onHistoryItemTapped(history, index)"
      @press.stop="onHistoryItemPressed(history, index)"
    >
      {{history}}
    </span>
  </transition-group>
</template>

<style scoped>
#page-home-search-history {
  display: flex;
  flex-wrap: wrap;
  padding: 4px;
  min-height: 48px;
}

#page-home-search-history>span {
  display: block;
  padding: 0 16px;
  margin: 4px;
  max-width: calc(100vw - 16px);
  font-size: 12px;
  line-height: 32px;
  background-color: var(--background-color-darker);
  border-radius: 16px;
}

.search-history-enter-active {
  animation: bounceIn 0.5s both;
}

.search-history-leave-active {
  position: absolute !important;
  animation: bounceOut 0.5s both;
}

.search-history-move {
  transition: transform 0.5s;
}
</style>

<script>
import AnyTouch from 'any-touch'

export default {
  name: 'PageHomeSearchHistory',
  props: {
    searchHistory: {
      type: Array,
      required: true
    }
  },
  mounted () {
    // 初始化手势插件
    this.initAnyTouch()
  },
  methods: {
    // 初始化手势插件
    initAnyTouch () {
      const at = new AnyTouch(this.$el)
      this.$on('hook:destroyed', () => {
        at.destroy()
      })
    },
    // 响应搜索历史项点击事件
    onHistoryItemTapped (history, index) {
      this.$emit('history-item-tapped', history, index)
    },
    // 响应搜索历史项长按事件
    onHistoryItemPressed (history, index) {
      this.$emit('history-item-pressed', history, index)
    }
  }
}
</script>
