<template>
  <transition
    enter-active-class="animate__animated animate__faster customized__fadeInDown"
    leave-active-class="animate__animated animate__faster customized__fadeOutUp"
  >
    <div
      id="page-home-search"
      v-show="show"
      @click.stop
    >
      <transition
        enter-active-class="animate__animated animate__faster animate__fadeInUp"
        leave-active-class="leave-absolute animate__animated animate__faster animate__fadeOutDown"
      >
        <!-- 历史记录 -->
        <PageHomeSearchHistory
          id="page-home-search-history"
          v-if="showSearchHistory"
          :search-history="searchHistory"
          @history-item-tapped="onHistoryItemTapped"
          @history-item-pressed="onHistoryItemPressed"
        />
        <!-- 搜索建议 -->
        <PageHomeSearchSuggestion
          v-else
          id="page-home-search-suggestion"
          :search-suggestion="searchSuggestion"
          @suggestion-item-clicked="onSuggestionItemClicked"
        />
      </transition>
      <!-- 搜索框 -->
      <div id="page-home-search-search-bar">
        <Search
          v-model="searchInput"
          placeholder="搜索或输入网址"
          shape="round"
          show-action
          :maxlength="30"
          @input="onSearchInput"
          @cancel="onSearchCancel"
          @search="onSearchConfirmed"
        />
      </div>
    </div>
  </transition>
</template>

<style scoped>
.customized__fadeInDown {
  animation-name: customized__fadeInDown;
}

.customized__fadeOutUp {
  animation-name: customized__fadeOutUp;
}

@keyframes customized__fadeInDown {
  from {
      opacity: 0;
      transform: translate3d(0, calc(-100% + 300px), 0);
  }
  to {
      opacity: 1;
      transform: translate3d(0, 0, 0);
  }
}

@keyframes customized__fadeOutUp {
  from {
      opacity: 1;
  }
  to {
      opacity: 0;
      transform: translate3d(0, calc(-100% + 300px), 0);
  }
}

.leave-absolute {
  position: absolute !important;
  left: 0;
  bottom: 50px;
  width: 100%;
  z-index: -1;
}

#page-home-search {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  min-height: 100%;
  background-color: var(--background-color);
  z-index: 2;
}

#page-home-search-search-bar {
  z-index: 2;
}

#page-home-search-search-bar>.van-search {
  padding: 8px 0 8px 8px;
}
</style>

<script>
import { Search } from 'vant'
import jsonp from '@/utils/getJSONP.js'
import { urlSearchBaidu, urlSuggestionBaidu } from '@/api/requestURLs.js'
import PageHomeSearchHistory from '@/components/PageHomeSearchHistory.vue'
import PageHomeSearchSuggestion from '@/components/PageHomeSearchSuggestion.vue'

export default {
  name: 'PageHomeSearch',
  components: {
    Search,
    PageHomeSearchHistory,
    PageHomeSearchSuggestion
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      searchInput: '',
      searchHistory: [],
      searchSuggestion: [],
      searchHistoryMaximum: 15
    }
  },
  computed: {
    showSearchHistory () {
      return !this.searchSuggestion.length
    }
  },
  created () {
    // 读取搜索历史记录
    this.searchHistory = this.getLocalSearchHistory()
  },
  methods: {
    // 读取搜索历史记录
    getLocalSearchHistory () {
      const tempStr = localStorage.getItem('searchHistory')
      return tempStr ? JSON.parse(tempStr) : []
    },
    // 保存搜索历史记录
    setLocalSearchHistory (historyArray) {
      const tempStr = JSON.stringify(historyArray)
      localStorage.setItem('searchHistory', tempStr)
    },
    // 添加搜索历史记录项
    addSearchHistoryItem (history) {
      // 移除已有的相同记录项
      const oldIndex = this.searchHistory.indexOf(history)
      if (oldIndex >= 0) {
        this.searchHistory.splice(oldIndex, 1)
      }
      // 检查记录数量
      if (this.searchHistory.length >= this.searchHistoryMaximum) {
        this.searchHistory.pop()
      }
      // 添加新记录项
      this.searchHistory.unshift(history)
    },
    // 移除搜索历史记录项
    removeSearchHistoryItem (index) {
      this.searchHistory.splice(index, 1)
    },
    // 前往搜索结果
    jumpToSearchResult (input) {
      const pattern = /[\w\-_]+(\.[\w\-_]+)+([\w\-.,@?^=%&:/~+#]*[\w\-@?^=%&/~+#])?/
      if (pattern.test(input)) {
        if (input.startsWith('http://') || input.startsWith('https://')) {
          location.assign(input)
        } else {
          location.assign(`http://${input}`)
        }
      } else {
        location.assign(urlSearchBaidu.concat(input))
      }
    },
    // 响应搜索输入事件
    onSearchInput (value) {
      if (!value) {
        return this.searchSuggestion.splice(0, this.searchSuggestion.length)
      }
      const callbackName = `baiduSuggest${new Date().getTime()}`
      jsonp(urlSuggestionBaidu, callbackName, {
        wd: value,
        cb: callbackName
      }).then(response => {
        this.searchSuggestion = response.s
      }).catch(error => {
        console.log(error)
      })
    },
    // 响应取消搜索事件
    onSearchCancel () {
      this.$emit('update:show', false)
    },
    // 响应搜索确认事件
    onSearchConfirmed (value) {
      if (!value) {
        return
      }
      this.addSearchHistoryItem(value)
      this.setLocalSearchHistory(this.searchHistory)
      this.jumpToSearchResult(value)
    },
    // 响应搜索建议项点击事件
    onSuggestionItemClicked (suggestion) {
      this.addSearchHistoryItem(suggestion)
      this.setLocalSearchHistory(this.searchHistory)
      this.jumpToSearchResult(suggestion)
    },
    // 响应搜索历史项点击事件
    onHistoryItemTapped (history, index) {
      this.addSearchHistoryItem(history)
      this.setLocalSearchHistory(this.searchHistory)
      this.jumpToSearchResult(history)
    },
    // 响应搜索历史项长按事件
    onHistoryItemPressed (history, index) {
      this.removeSearchHistoryItem(index)
      this.setLocalSearchHistory(this.searchHistory)
    }
  }
}
</script>
