<template>
  <div
    id="page-home"
    @click="onPageClicked"
  >
    <!-- 背景图片 -->
    <VanImage
      v-show="showBackgroundImage"
      id="page-home-background"
      fit="cover"
      :src="backgroundInfo.bgImgDataURL"
    />
    <!-- 按钮栏 -->
    <div id="btns-container">
      <!-- 背景选择按钮 -->
      <Icon
        id="btn-background"
        class="btn-icon"
        name="photo"
        @click.stop="setPageHomeBackgroundChangeShown"
      />
      <!-- 跳转按钮 -->
      <Icon
        id="btn-settings"
        class="btn-icon"
        name="setting"
        @click.stop="jumpToPageSettings"
      />
    </div>
    <!-- 页面图标 -->
    <img
      id="search-logo"
      src="@/assets/others/google_logo.png"
      alt="search-bar-logo"
    />
    <!-- 可隐藏区域 -->
    <transition
      enter-active-class="animate__animated animate__faster animate__fadeInUp"
      leave-active-class="animate__animated animate__faster animate__fadeOutDown"
    >
      <div v-show="!showPageHomeSearch">
        <!-- 搜索框 -->
        <div
          id="search-box"
          @click.stop="setPageHomeSearchShown"
        ></div>
        <!-- 书签列表 -->
        <draggable
          v-model="userBookmarks"
          id="bookmarks-container"
          handle=".bookmark-draggable"
        >
          <transition-group
            name="bookmark"
          >
            <!-- 当前书签 -->
            <BaseBookmark
              v-for="(item, index) in userBookmarks"
              :key="item.iconName"
              :url="item.url"
              :title="item.title"
              :title-color="bookmarkTitleColor"
              :icon-src="item.iconSrc"
              :removable="showBtnRemove"
              class="bookmark-draggable"
              @tap="onBookmarkTapped(item.url)"
              @press="onBookmarkPressed"
              @remove="onBookmarkRemoved(index)"
            />
            <!-- 添加按钮 -->
            <BaseBookmark
              v-if="showBtnAdd"
              id="btn-bookmark-add"
              key="btn-bookmark-add"
              @tap="onBtnAddBookmarkTapped"
            >
              <template #icon>
                <Icon
                  id="btn-bookmark-add-icon"
                  name="plus"
                />
              </template>
            </BaseBookmark>
          </transition-group>
        </draggable>
      </div>
    </transition>
    <!-- 搜索弹出层 -->
    <PageHomeSearch
      :show.sync="showPageHomeSearch"
    />
    <!-- 书签添加弹出层 -->
    <PageHomeBookmarkAdd
      :show.sync="showPopupBookmarkAdd"
      @bookmark-added="onBookmarkAdded"
    />
    <!-- 背景添加弹出层 -->
    <PageHomeBackgroundChange
      :show.sync="showPopupBackgroundChange"
      :background-info="backgroundInfo"
      @background-changed="onBackgroundChanged"
    />
  </div>
</template>

<style scoped>
#page-home {
  padding: 0 24px;
  height: 100%;
  background-color: white;
  overflow: hidden;
}

#page-home-background {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

#btns-container {
  display: flex;
  justify-content: space-between;
  margin-top: 24px;
}

#btns-container .btn-icon {
  width: 32px;
  font-size: 22px;
  line-height: 32px;
  text-align: center;
  color: var(--icon-color);
  background-color: var(--background-color);
  border-radius: 50%;
}

#search-logo {
  display: block;
  margin: 64px auto 24px;
  width: 150px;
  height: auto;
}

#search-box {
  height: 48px;
  background-color: var(--background-color);
  border: 1px solid var(--border-color-darker);
  border-radius: 24px;
  opacity: 0.7;
}

#bookmarks-container {
  font-size: 0;
  margin: 16px 0;
}

#bookmarks-container .bookmark:not(:nth-child(5n)) {
  margin-right: calc((100% - 48px * 5) / 4);
}

.bookmark-enter-active {
  animation: bounceIn 0.5s both;
}

.bookmark-leave-active {
  position: absolute !important;
  animation: bounceOut 0.5s both;
}

.bookmark-move {
  transition: transform 0.5s;
}

#btn-bookmark-add-icon {
  width: 48px;
  font-size: 22px;
  line-height: 48px;
  color: var(--icon-color);
  background-color: var(--background-color);
  border-radius: 50%;
}
</style>

<script>
import draggable from 'vuedraggable'
import { Dialog, Icon, Image as VanImage, Notify } from 'vant'
import BaseBookmark from '@/components/BaseBookmark.vue'
import PageHomeSearch from '@/components/PageHomeSearch.vue'
import PageHomeBookmarkAdd from '@/components/PageHomeBookmarkAdd.vue'
import PageHomeBackgroundChange from '@/components/PageHomeBackgroundChange.vue'

export default {
  name: 'PageHome',
  components: {
    Icon,
    VanImage,
    draggable,
    BaseBookmark,
    PageHomeSearch,
    PageHomeBookmarkAdd,
    PageHomeBackgroundChange
  },
  data () {
    return {
      showBtnAdd: false,
      showBtnRemove: false,
      showPageHomeSearch: false,
      showPopupBookmarkAdd: false,
      showPopupBackgroundChange: false,
      maxBookmarkNumber: 20
    }
  },
  computed: {
    userBookmarks: {
      get () {
        return this.$store.state.appUserBookmarks
      },
      set (value) {
        // 更新用户书签顺序，但不更新数据库内书签图标
        this.$store.dispatch('updateAppUserBookmarks', value)
      }
    },
    backgroundInfo () {
      return this.$store.state.appUserBackground
    },
    showBackgroundImage () {
      return Boolean(this.backgroundInfo.bgImgDataURL)
    },
    bookmarkTitleColor () {
      return this.backgroundInfo.bmTitleColor
    }
  },
  created () {
    // 响应不支持IndexedDB/WebSQL事件
    this.onDatabaseUnsupported()
  },
  methods: {
    // 跳转道设置页
    jumpToPageSettings () {
      this.$router.push('/settings')
        .catch(() => {})
    },
    // 响应不支持IndexedDB/WebSQL事件
    onDatabaseUnsupported () {
      this.$store.getters.isDatabaseSupported || Dialog.alert({
        title: '警告',
        message: '您的浏览器不支持或已禁用IndexedDB/WebSQL，部分图片文件可能无法正常存取'
      })
    },
    // 显示搜索弹出层
    setPageHomeSearchShown () {
      this.showPageHomeSearch = true
    },
    // 全页点击事件
    onPageClicked () {
      this.showBtnAdd = this.showBtnRemove ? false : !this.showBtnAdd
      this.showBtnRemove = false
    },
    // 新建书签按钮点击事件
    onBtnAddBookmarkTapped () {
      this.showPopupBookmarkAdd = this.userBookmarks.length < this.maxBookmarkNumber
      this.showPopupBookmarkAdd || Notify({
        type: 'warning',
        message: `最多添加${this.maxBookmarkNumber}个书签`
      })
    },
    // 书签点击事件
    onBookmarkTapped (url) {
      url && !this.removable && location.assign(url)
    },
    // 书签长按事件
    onBookmarkPressed () {
      this.showBtnRemove = true
      this.showBtnAdd = true
    },
    // 书签新增事件
    onBookmarkAdded (bookmark) {
      this.$store.dispatch('saveAppUserBookmark', bookmark)
        .catch(() => {
          Notify({
            type: 'danger',
            message: '保存书签图标失败'
          })
        })
    },
    // 书签移除事件
    onBookmarkRemoved (index) {
      this.$store.dispatch('deleteAppUserBookmark', index)
        .catch(() => {
          Notify({
            type: 'danger',
            message: '移除书签失败'
          })
        })
    },
    // 显示背景添加弹出层
    setPageHomeBackgroundChangeShown () {
      this.showPopupBackgroundChange = true
    },
    // 响应背景修改事件
    onBackgroundChanged (background) {
      this.$store.dispatch('saveAppUserBackground', background)
    }
  }
}
</script>
