<template>
  <div
    id="page-home-bookmark-add"
    @click.stop
  >
    <Popup
      v-model="showPopup"
      position="bottom"
      closeable
      :close-on-click-overlay="false"
      @open="getRandomColorInPalette"
      @close="resetFormState"
    >
      <div
        id="page-home-bookmark-add-title"
        class="van-hairline--bottom"
      >
        添加书签
      </div>
      <Form
        id="page-home-bookmark-add-form"
        ref="pageHomeBookmarkAddForm"
        :show-error="false"
        @submit="onSubmitFormValues"
      >
        <Field
          ref="pageHomeBookmarkAddFieldTitle"
          v-model="formValues.bookmarkTitle"
          name="bookmarkTitle"
          label="标题"
          placeholder="请输入1至30个字符（字母/数字/汉字）"
          :rules="rulesBookmarkTitle"
        />
        <Field
          ref="pageHomeBookmarkAddFieldTitle"
          v-model="formValues.bookmarkURL"
          name="bookmarkURL"
          label="地址"
          placeholder="请输入有效的URL（不超过250个字符）"
          :rules="rulesBookmarkURL"
        />
        <Field
          ref="pageHomeBookmarkAddFieldIcon"
          label="图标"
        >
          <template #input>
            <transition
              enter-active-class="animate__animated animate__faster animate__fadeIn"
              leave-active-class="animate__animated animate__faster animate__fadeOut"
              mode="out-in"
            >
              <Uploader
                v-if="showVantIconPicker"
                v-model="formValues.bookmarkIconList"
                :max-count="1"
                :max-size="maxIconFileSize"
                :before-read="checkBookmarkIconType"
                @oversize="onIconFileOversize"
              />
              <BaseText2Icon
                v-else
                :icon-text="formValues.bookmarkTitle"
                :icon-color="iconColor"
                :palette="palette"
                @icon-color-changed="onIconColorSelected"
                @icon-data-updated="onIconDataUpdated"
              />
            </transition>
          </template>
          <template #button>
            <Icon
              id="page-home-bookmark-add-btn-switch-icon-picker"
              name="exchange"
              @click="switchIconPicker"
            />
          </template>
        </Field>
        <div id="page-home-bookmark-add-btns">
          <Button
            round
            block
            type="info"
            native-type="submit"
          >
            确认添加
          </Button>
        </div>
      </Form>
    </Popup>
  </div>
</template>

<style scoped>
#page-home-bookmark-add-title {
  font-size: 16px;
  line-height: 54px;
  text-align: center;
}

#page-home-bookmark-add-btn-switch-icon-picker {
  width: 40px;
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  color: white;
  background-color: var(--primary-color);
  border-radius: 50%;
}

#page-home-bookmark-add-btns {
  padding: 24px 16px;
}
</style>

<script>
import { Button, Field, Form, Icon, Popup, Toast, Uploader } from 'vant'
import BaseText2Icon from '@/components/BaseText2Icon.vue'

export default {
  name: 'PageHomeBookmarkAdd',
  components: {
    Button,
    Field,
    Form,
    Icon,
    Popup,
    Uploader,
    BaseText2Icon
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      showVantIconPicker: false,
      iconColor: '#f44336',
      palette: ['#f44336', '#e91e63', '#9c27b0', '#673ab7', '#3f51b5', '#2196f3', '#03a9f4', '#00bcd4', '#009688'],
      formValues: {
        bookmarkTitle: '',
        bookmarkURL: '',
        bookmarkIconList: [],
        bookmarkTextIcon: {}
      },
      maxIconFileSize: 2 << 20,
      rulesBookmarkTitle: [
        {
          required: true,
          message: '请填写书签标题'
        },
        {
          message: '',
          validator: this.checkBookmarkTitle
        }
      ],
      rulesBookmarkURL: [
        {
          required: true,
          message: '请填写书签地址'
        },
        {
          message: '',
          validator: this.checkBookmarkURL
        }
      ]
    }
  },
  computed: {
    showPopup: {
      get () {
        return this.show
      },
      set (value) {
        this.$emit('update:show', value)
      }
    }
  },
  methods: {
    // 检查书签标题格式
    checkBookmarkTitle (value, rule) {
      const pattern = /^[\u4e00-\u9fa5\w-]+$/
      let checkPassed = false
      if (!pattern.test(value)) {
        rule.message = '只能使用：字母/数字/汉字'
      } else if (value.length < 1) {
        rule.message = '字符不应少于1个'
      } else if (value.length > 30) {
        rule.message = '字符不应多于30个'
      } else {
        checkPassed = true
      }
      return checkPassed
    },
    // 检查书签地址格式
    checkBookmarkURL (value, rule) {
      if (!value.startsWith('https://') && !value.startsWith('http://')) {
        value = `http://${value}`
        this.formValues.bookmarkURL = value
      }
      const pattern = /^https?:\/\/(([a-zA-Z0-9_-])+(\.)?)*(:\d+)?(\/((\.)?(\?)?=?&?[a-zA-Z0-9_-](\?)?)*)*$/i
      let checkPassed = false
      if (!pattern.test(value)) {
        rule.message = '请输入有效的URL'
      } else if (value.length > 30) {
        rule.message = '字符不应多于250个'
      } else {
        checkPassed = true
      }
      return checkPassed
    },
    // 检查书签图标类型
    checkBookmarkIconType (file) {
      if (['image/jpeg', 'image/png'].includes(file.type)) {
        return true
      } else {
        Toast.fail('请上传jpeg/jpg/png格式的图片')
        return false
      }
    },
    // 图标文件大小超出限制事件
    onIconFileOversize () {
      Toast.fail(`文件大小不应超过${this.maxIconFileSize >> 20}MB`)
    },
    // 重置表单状态
    resetFormState () {
      // 重置表单显示
      Object.assign(this.formValues, {
        bookmarkTitle: '',
        bookmarkURL: '',
        bookmarkIconList: []
      })
      // 重置表单校验
      this.$refs.pageHomeBookmarkAddForm.resetValidation()
    },
    // 切换图标文件选择器
    switchIconPicker () {
      this.showVantIconPicker = !this.showVantIconPicker
    },
    // 获取调色盘内随机颜色
    getRandomColorInPalette () {
      const tempIndex = (Math.random() * (this.palette.length - 1)).toFixed(0)
      this.iconColor = this.palette[tempIndex]
    },
    // 响应文字图片生成组件的颜色选择事件
    onIconColorSelected (color) {
      this.iconColor = color
    },
    // 响应文字图片生成组件的图标数据更新事件
    onIconDataUpdated (data) {
      this.formValues.bookmarkTextIcon = data
    },
    // 获取依据时间戳的的图标文件名
    getIconFileNameByTimestamp (fileType) {
      return `${new Date().getTime()}.${fileType.split('/')[1]}`
    },
    // 提交表单数据事件
    onSubmitFormValues (values) {
      let iconFile, iconDataURL
      if (this.formValues.bookmarkIconList.length > 0 && this.showVantIconPicker) {
        // 取得图标文件
        iconFile = this.formValues.bookmarkIconList[0].file
        // 取得图标文件的base64编码
        iconDataURL = this.formValues.bookmarkIconList[0].content
      } else {
        // 取得图标文件
        iconFile = this.formValues.bookmarkTextIcon.iconFile
        // 取得图标文件的base64编码
        iconDataURL = this.formValues.bookmarkTextIcon.iconDataURL
      }
      // 取得图标文件新文件名
      const iconName = this.getIconFileNameByTimestamp(iconFile.type)
      // 设置抛出的对象
      const bookmarkInfo = {
        title: values.bookmarkTitle,
        url: values.bookmarkURL,
        iconName: iconName,
        iconSrc: iconDataURL,
        iconFile: iconFile
      }
      // 触发新书签添加事件
      this.$emit('bookmark-added', bookmarkInfo)
      // 关闭弹出层
      this.showPopup = false
    }
  }
}
</script>
